/*buttons styles*/

.action-button {
  @extend %heart-beat-border-animation;

  padding: 5px 20px;
  border-width: 1px;
  border-style: solid;
  border-radius: 2px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.9rem;
  line-height: 1.8rem;
  font-weight: 400;
  background-repeat: no-repeat;
  background-color: #00a5c9;
  border-color: #00a5c9;
  color: white;
  margin-right: 7px;

  &.with-icon svg {
    vertical-align: middle;
    height: 1rem;
    width: 1rem;
    margin-right: 3px;
    fill: white;
  }

  &.secondary {
    background-color: #fff;
    border-color: #00a5c9;
    color: #00a5c9;
    &.with-icon {
      svg {
        fill: #00a5c9;
      }
    }
  }
}

.icon-button {
  display: inline-block;
  height: auto;
  width: 29px;
  margin-left: 12px;

  svg {
    fill: $theme-primary-background-color;
  }
}